
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

export default {

directives: {
TransferDom
},

components: {
XInput,
XButton,
Group,
XSwitch,
Popup,
Cell
},
data() {
return {
account: '',
password: '',
list: 'true',
ask: '',
questions: [],
expert_btn: true,
bottom_btn: false,
experts: new Array(),
modal6: false,
selectExpert: new Array(),
loading: true
}
},
methods: {
doAsk() {
var date = new Date()
this.$http.post(`http://183.131.126.105:9000/basic/data/manage?key=i_comapi_st_qustion`, {
user_id: this.$store.state.user_id,
question: this.ask,
time: date.toLocaleDateString()
})
.then( (response)=> {
this.findQustion()
this.ask=''
})
.catch(function (error) {
console.log(error);
})
},
doQuestion(item) {
console.log(item)
this.$router.push({
path: '/question',
query: {
question: item
}
})
},
toPersonal() {
this.$router.push({
path: '/personal'
})
},
doexpert() {
this.bottom_btn = !this.bottom_btn
this.list = !this.list
},
selectExperts(item) {
this.modal6 = true
this.selectExpert = item
},

findQustion() {
let that = this
this.$http.get(
`/api/basic/data?key=i_comapi_st_qustion&user_id=${that.$store.state.user_id}`
)
.then((response) => {
console.log(response)
this.questions=response.data.data
this.questions.reverse()
})
.catch(function (error1) {
console.log(error1);
});
},
findExpert() {
let that = this
this.$http.get(
`/api/basic/data?key=s_comapi_st_expertsList`
)
.then((response) => {
this.experts = response.data.data;
console.log(this.experts)
})
.catch(function (error1) {
console.log(error1);
});
}
},
watch:{

},
mounted() {
this.findQustion();
this.findExpert();
}
}




